import request from '@/utils/axios'




//a攻击能力趋势查询
export function trend(params) {
  return request({
    url: '/power/a/trend',
    method: 'post',
    params
  })
}

//a攻击能力分析
export function aAnalysis(params) {
  return request({
    url: '/power/a',
    method: 'post',
    params
  })
}






