<template>
  <div id="brokenLine5" class="brokenLine5"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    loadChart(title,hp, hpw, p10, p10w, p12, p12w, p15, p15w) {
      this.myChart = echarts.init(document.getElementById("brokenLine5"));
      this.myChart.setOption(
        this.initOption(title,hp, hpw, p10, p10w, p12, p12w, p15, p15w)
      );
    },
    init(data) {
      var hp = [];
      var hpw = [];
      var p10 = [];
      var p10w = [];
      var p12 = [];
      var p12w = [];
      var p15 = [];
      var p15w = [];

      var title = [];
      for (var j in data) {
        title.push(j);
        hp.push(
          data[j] && data[j].distance[255][0] ? (data[j].distance[255][0]/1000).toFixed(2) : ""
        );
        hpw.push(
          data[j] && data[j].distance[255][1] ? (data[j].distance[255][1]/1000).toFixed(2) : ""
        );

        p10.push(
          data[j] && data[j].distance[10][0] ? (data[j].distance[10][0]/1000).toFixed(2) : ""
        );
        p10w.push(
          data[j] && data[j].distance[10][1] ? (data[j].distance[10][1]/1000).toFixed(2) : ""
        );

        p12.push(
          data[j] && data[j].distance[12][0] ? (data[j].distance[12][0]/1000).toFixed(2) : ""
        );
        p12w.push(
          data[j] && data[j].distance[12][1] ? (data[j].distance[12][1]/1000).toFixed(2) : ""
        );

        p15.push(
          data[j] && data[j].distance[15][0] ? (data[j].distance[15][0]/1000).toFixed(2) : ""
        );
        p15w.push(
          data[j] && data[j].distance[15][1] ? (data[j].distance[15][1]/1000).toFixed(2) : ""
        );
      }

      this.loadChart(title,hp, hpw, p10, p10w, p12, p12w, p15, p15w);
    },
    initOption(title,hp, hpw, p10, p10w, p12, p12w, p15, p15w) {
      return {
        title: {
          text: "发射平均命中距离趋势图",
          left: "center",
          top: 10,
          textStyle: {
            color: "#05fad5"
          }
        },
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          left: "center",
          bottom: 0,
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: [
            "航炮平均中距离",
            "P-10平均中距离",
            "P-12平均中距离",
            "P-15平均中距离",
          ]
        },
        grid: {
          left: "3%",
          bottom: "15%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#05fad5"
              }
            },
            data: title
          }
        ],
        yAxis: {
          min: 0,
          max: 100,
          name:"km",
          interval: 20,
          axisLine: {
            lineStyle: {
              color: "#05fad5"
            }
          }
        },
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: [
          {
            name: "航炮平均中距离",
            type: "line",
            data: hp
          },
          {
            name: "P-10平均中距离",
            type: "line",
            data: p10
          },
          {
            name: "P-12平均中距离",
            type: "line",
            data: p12
          },
          {
            name: "P-15平均中距离",
            type: "line",
            data: p15
          },
        ]
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine5 {
  width: 100%;
  height: 100%;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
}
</style>
