<template>
  <div id="brokenLine7" class="brokenLine7"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null,
      title: []
    };
  },
  mounted() {},

  methods: {
    loadChart(hp, hpw) {
      this.myChart = echarts.init(document.getElementById("brokenLine7"));
      this.myChart.setOption(this.initOption(hp, hpw));
    },
    init(data, type) {
      var hp = [];
      var hpw = [];
      var wqType = type==0?'255':type==1?'10':type == 2?'12':'15';
      var title = [];
      for (var j in data) {
        title.push(j);
        var s = data[j] ? data[j].weapon[wqType][0] : 0
        var f = data[j] ? data[j].weapon[wqType][1] : 0
        var z = s + f;
        var sgl = isNaN(s/z)?0:(s/z)*100;
        var fgl = isNaN(f/z)?0:(f/z)*100;
        hp.push(sgl);
        hpw.push(fgl);
      }
      this.title = title;
      this.loadChart(hp, hpw);

    },
    initOption(hp, hpw) {
      return {
        title: {
          text: "命中概率趋势图",
          top: 17,
          left: 60,
          textStyle: {
            color: "#05fad5"
          }
        },
        tooltip: {
          trigger: "axis",
          formatter: function(seriesData, ticket, callback) {
            var showHtm = "";
            for (var i = 0; i < seriesData.length; i++) {
              var name = seriesData[i].seriesName;
              var text = seriesData[i].name;
              var value = seriesData[i].value;
              if (i == 0) {
                showHtm = text + "<br>";
              }
              showHtm += name + ": " + value + "%" + "<br>";
            }
            return showHtm;
          },
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          left: "center",
          bottom: 0,
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: ["命中率", "未命中率"]
        },
        grid: {
          left: "3%",
          bottom: "7%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#05fad5"
              }
            },
            data: this.title
          }
        ],
        yAxis: {
          min: 0,
          max: 100,
          interval: 20,
          name:"%",
          axisLine: {
            lineStyle: {
              color: "#05fad5"
            }
          }
        },
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: [
          {
            name: "命中率",
            type: "line",
            data: hp
          },
          {
            name: "未命中率",
            type: "line",
            data: hpw
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine7 {
  width: 100%;
  height: 100%;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
}
</style>
