<template>
  <div id="brokenLine2" class="brokenLine2"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null,
      time:[]
    };
  },
  mounted() {},

  methods: {
    loadChart(title, data) {
      this.myChart = echarts.init(document.getElementById("brokenLine2"));
      this.myChart.clear();
      this.myChart.setOption(this.initOption(title, data));
    },

    init(data, type) {
      if (type == 0) {
        type = "255";
      } else if (type == 1) {
        type = "10";
      } else if (type == 2) {
        type = "12";
      } else if (type == 3) {
        type = "15";
      }
      var title = [];
      var series = [];
      var obj = {};
      var time = [];
      for (var j in data) {
        time.push(j);
        for (var k in data[j].cause[type]) {
          var val = data[j].cause[type][k];
          if (obj[k]) {
            obj[k].data.push(data[j].cause[type][k]);
          } else {
            obj[k] = {
              name: k,
              type: "line",
              data: [data[j].cause[type][k]]
            };
          }
          title.push(k);
        }
      }
      this.time = time;
      for (var i in obj) {
        series.push(obj[i]);
      }
      this.loadChart(title, series);
    },

    initOption(title, data) {
      return {
        title: {
          text: "未命中原因趋势图",
          top: 0,
          left: 20,
          textStyle: {
            color: "#05fad5"
          }
        },
        tooltip: {
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          left: "center",
          bottom: 0,
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: title
        },
        grid: {
          left: "3%",
          bottom: "12%",
          height: 270,
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#05fad5"
              }
            },
            data: this.time
          }
        ],
        yAxis: {
          min: 0,
          max: 10,
          name:"次",
          interval: 2,
          axisLine: {
            lineStyle: {
              color: "#05fad5"
            }
          }
        },
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: data
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine2 {
  width: 100%;
  height: 100%;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
}
</style>
