<template>
  <div id="brokenLine6" class="brokenLine6" style="height: 370px;"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    loadChart(title, hp, hpw, p10, p10w, p12, p12w, p15, p15w) {
      this.myChart = echarts.init(document.getElementById("brokenLine6"));
      this.myChart.setOption(
        this.initOption(title, hp, hpw, p10, p10w, p12, p12w, p15, p15w)
      );
    },
    init(data) {
      var hp = [];
      var hpw = [];
      var p10 = [];
      var p10w = [];
      var p12 = [];
      var p12w = [];
      var p15 = [];
      var p15w = [];

      var title = [];
      for (var j in data) {
        title.push(j);

        if (data[j]) {
          var m = data[j].weapon[255][0];
          var w = data[j].weapon[255][1];
          var z = m + w;
          hp.push(isNaN(m/z)?0:(m/z)*100);
          hpw.push(isNaN(w/z)?0:(w/z)*100);
        } else {
          hp.push(0);
          hpw.push(0);
        }

        if (data[j]) {
          var m = data[j].weapon[10][0];
          var w = data[j].weapon[10][1];
          var z = m + w;
          p10.push(isNaN(m/z)?0:(m/z)*100);
          p10w.push(isNaN(w/z)?0:(w/z)*100);
        } else {
          p10.push(0);
          p10w.push(0);
        }

        if (data[j]) {
          var m = data[j].weapon[12][0];
          var w = data[j].weapon[12][1];
          var z = m + w;
          p12.push(isNaN(m/z)?0:(m/z)*100);
          p12w.push(isNaN(w/z)?0:(w/z)*100);
        } else {
          p12.push(0);
          p12w.push(0);
        }

        if (data[j]) {
          var m = data[j].weapon[15][0];
          var w = data[j].weapon[15][1];
          var z = m + w;
          p15.push(isNaN(m/z)?0:(m/z)*100);
          p15w.push(isNaN(w/z)?0:(w/z)*100);
        } else {
          p15.push(0);
          p15w.push(0);
        }
      }
      this.loadChart(title, hp, hpw, p10, p10w, p12, p12w, p15, p15w);
    },
    initOption(title, hp, hpw, p10, p10w, p12, p12w, p15, p15w) {
      return {
        title: {
          text: "总命中率趋势图",
          left: "center",
          top: 10,
          textStyle: {
            color: "#05fad5"
          }
        },
        tooltip: {
          trigger: "axis",
          formatter: function(seriesData, ticket, callback) {
            var showHtm = "";
            for (var i = 0; i < seriesData.length; i++) {
              var name = seriesData[i].seriesName;
              var text = seriesData[i].name;
              var value = seriesData[i].value;
              if (i == 0) {
                showHtm = text + "<br>";
              }
              showHtm += name + ": " + value + "%" + "<br>";
            }
            return showHtm;
          },
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985"
            }
          }
        },
        legend: {
          left: "center",
          bottom: 0,
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: [
            "航炮总命中率",
            "航炮总未命中率",
            "P-10总命中率",
            "P-10总未命中率",
            "P-12总命中率",
            "P-12总未命中率",
            "P-15总命中率",
            "P-15总未命中率"
          ]
        },
        grid: {
          left: "3%",
          bottom: "15%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                color: "#05fad5"
              }
            },
            data: title
          }
        ],
        yAxis: {
          min: 0,
          max: 100,
          name: "%",
          interval: 20,
          axisLine: {
            lineStyle: {
              color: "#05fad5"
            }
          }
        },
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: [
          {
            name: "航炮总命中率",
            type: "line",
            data: hp
          },
          {
            name: "航炮总未命中率",
            type: "line",
            data: hpw
          },
          {
            name: "P-10总命中率",
            type: "line",
            data: p10
          },
          {
            name: "P-10总未命中率",
            type: "line",
            data: p10w
          },
          {
            name: "P-12总命中率",
            type: "line",
            data: p12
          },
          {
            name: "P-12总未命中率",
            type: "line",
            data: p12w
          },
          {
            name: "P-15总命中率",
            type: "line",
            data: p15
          },
          {
            name: "P-15总未命中率",
            type: "line",
            data: p15w
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine6 {
  width: 100%;
  height: 370px;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  overflow: hidden;
}
</style>
