<template>
  <section class="data_section" ref="data_section">
    <h3 class="aTitle">攻击能力变化趋势</h3>
    <div class="headerData">
      <el-select v-model="data.uid" @change="selectGet" filterable placeholder="请选择人员">
        <el-option
          v-for="item in options"
          v-if="item.mode !='1'"
          :key="item._id"
          :label="item.name"
          :value="item._id"
        ></el-option>
      </el-select>
      <el-date-picker
        class="dateWrapper"
        v-model="value1"
        type="datetimerange"
        range-separator="至"
        start-placeholder="开始日期"
        end-placeholder="结束日期"
      ></el-date-picker>
      <el-button type="primary" class="search" @click="search(4)" icon="el-icon-search">查询</el-button>
    </div>
    <el-row :gutter="10" class="row_list order_list">
      <el-col :span="12" class="searchWrapper">
        <el-button class="but1 search" type="primary" @click="search(2)">总体数据</el-button>
        <el-button type="primary" class="search" @click="search(0)">前仓数据</el-button>
        <el-button type="primary" class="search" @click="search(1)">后仓数据</el-button>
      </el-col>
      <el-col :span="6">&nbsp</el-col>
      <!-- <el-col :span="6">
        <ul class="df">
          <li>
            <p>总得分：</p>
            <p>1000000</p>
          </li>
          <li>
            <p>局平均得分:</p>
            <p>60</p>
          </li>
        </ul>
      </el-col>-->
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <div class="aTopLeft" id="aTopLeft">
        <brokenLine ref="brokenLine"></brokenLine>
      </div>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <div class="aTopLeft">
        <brokenLine6 ref="brokenLine6"></brokenLine6>
      </div>
      <div class="aTopRight aButtomRight" id="aButtomRight">
        <!-- <ul class="change">
          <li>航炮</li>
          <li style="background:#0ea9a9">P-10</li>
          <li>P-12</li>
          <li>P-15</li>
        </ul>-->
        <ul class="change">
          <li
            v-for="(item,index) in navtitles"
            :key="index"
            @click="alertTabs(item,index)"
            :class="{active:nowIndex==index}"
          >{{item}}</li>
        </ul>
        <brokenLine7 ref="brokenLine7"></brokenLine7>
      </div>
    </el-row>
    <el-row :gutter="10" class="row_list order_list">
      <div class="aTopLeft">
        <brokenLine5 ref="brokenLine5"></brokenLine5>
      </div>
      <div class="aTopRight aButtomRight" id="aButtomRight">
        <ul class="change">
          <li
            v-for="(item,index) in navtitles"
            :key="index"
            @click="alertTabs2(item,index)"
            :class="{active2:nowIndex2==index}"
          >{{item}}</li>
        </ul>
        <brokenLine2 ref="brokenLine2"></brokenLine2>
      </div>
    </el-row>
  </section>
</template>
<script>
import { trend } from "@/api/a";
import { formatDate } from "@/utils/mUtils";
import brokenLine from "./aTrend/brokenLine";
import brokenLine5 from "./aTrend/brokenLine5";
import brokenLine6 from "./aTrend/brokenLine6";
import brokenLine7 from "./aTrend/brokenLine7";
import brokenLine2 from "./aTrend/brokenLine2";
export default {
  components: {
    brokenLine,
    brokenLine5,
    brokenLine6,
    brokenLine7,
    brokenLine2
  },
  data() {
    return {
      value1: "",
      nowIndex: 0,
      nowIndex2: 0,
      navtitles: ["航炮", "P-10", "P-12", "P-15"],
      user: this.sessionData("get", "user"),
      data: {
        uid: "",
        name: "",
        site: "2",
        start: "",
        end: ""
      },
      dataObj: {},
      options: this.sessionData("get", "userList")
    };
  },
  created() {},
  mounted() {
    this.trend(this.user.name, this.data.site);
  },
  methods: {
    alertTabs(i, index) {
      this.nowIndex = index;
      if (this.dataObj) {
        this.$refs.brokenLine7.init(this.dataObj, index);
      }
    },

    alertTabs2(i, index) {
      this.nowIndex2 = index;
      if (this.dataObj) {
        this.$refs.brokenLine2.init(this.dataObj, index);
      }
    },

    search(s) {
      s = s == 4 ? this.data.site : s;
      var st = this.value1 ? this.value1[0].getTime() : "";
      var et = this.value1 ? this.value1[1].getTime() : "";
      this.trend(this.data.name, s, st, et);
    },
    selectGet(vId) {
      let obj = {};
      obj = this.options.find(item => {
        return item._id === vId;
      });
      this.data.name = obj.name;
    },
    trend(name, site, start, end) {
      trend({
        name: name || this.user.name,
        site: site,
        start: start || "",
        end: end || ""
      }).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "数据查询成功！",
            type: "success"
          });
          var list = this.$unfly(res.data);
          console.log("攻击能力趋势图", list);
          var obj = {};
          for (var i in list) {
            var y = formatDate(new Date(list[i].start),5);
            obj[y] = list[i].data;
          }
          this.dataObj = obj;
          this.$refs.brokenLine.init(obj);
          this.$refs.brokenLine5.init(obj); //发射平均距离趋势图
          this.$refs.brokenLine6.init(obj);
          this.$refs.brokenLine7.init(obj, 0);
          this.$refs.brokenLine2.init(obj, 0);
          
        }
      });
    }
  }
};
</script>

