<template>
  <div id="brokenLine" class="brokenLine"></div>
</template>

<script>
import echarts from "echarts";
export default {
  data() {
    return {
      myChart: null
    };
  },
  mounted() {},

  methods: {
    loadChart(title,s, f, p) {
      this.myChart = echarts.init(document.getElementById("brokenLine"));
      this.myChart.setOption(this.initOption(title,s, f, p));
    },

    init(data) {
      console.log("省扶贫：",data)
      var s = [];
      var f = [];
      var p = [];
      var title = [];
      for (var i in data) {
        title.push(i);

        var sCS = data[i]&&data[i].winner ? data[i].winner:0;
        var fCS = data[i]&&data[i].loser ? data[i].loser:0;
        var pCS = data[i]&&data[i].tier ? data[i].tier:0;
        var zCS = sCS + fCS + pCS;

        s.push(isNaN(sCS/zCS)?0:(sCS/zCS*100).toFixed(2));
        f.push(isNaN(fCS/zCS)?0:(fCS/zCS*100).toFixed(2));
        p.push(isNaN(pCS/zCS)?0:(pCS/zCS*100).toFixed(2));



        // s.push(data[i] ? data[i].winner*100 : "");
        // f.push(data[i] ? data[i].loser*100 : "");
        // p.push(data[i] ? data[i].tier*100 : "");
      }
      this.loadChart(title,s, f, p);
    },

    initOption(title,s, f, p) {
      return {
        title: {
          text: "胜率趋势图",
          left: "50",
          top: 0,
          textStyle: {
            color: "#00FFFF"
          }
        },
        tooltip: {
          trigger: "axis",
          formatter: function(seriesData, ticket, callback) {
            var showHtm = "";
            for (var i = 0; i < seriesData.length; i++) {
              var name = seriesData[i].seriesName;
              var text = seriesData[i].name;
              var value = seriesData[i].value;
              if (i == 0) {
                showHtm = text + "<br>";
              }
              showHtm += name + ": " + value + "%" + "<br>";
            }
            return showHtm;
          },
          axisPointer: {
            type: "cross",
            crossStyle: {
              color: "#999"
            }
          }
        },
        legend: {
          textStyle: {
            color: "#05fad5",
            fontStyle: "normal",
            fontFamily: "微软雅黑",
            fontSize: 12
          },
          data: ["胜", "负", "平", "总命中率", "总未命中率"]
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: false,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            data: title
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "%",
            min: 0,
            max: 100,
            interval: 20,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
          },
          {
            type: "value",
            name: "%",
            min: 0,
            max: 100,
            interval: 20,
            axisLine: {
              lineStyle: {
                fontSize: 16,
                color: "#05fad5"
              }
            },
            axisLabel: {
              formatter: "{value}"
            }
          }
        ],
        color: ["#1ed674", "#ff6567", "#1c84ff", "#f9ff00", "#ffa9a8"],
        series: [
          {
            name: "胜",
            itemStyle: {
              barBorderRadius: 13
            },
            type: "bar",
            data: s
          },
          {
            name: "负",
            itemStyle: {
              barBorderRadius: 13
            },
            type: "bar",
            data: f
          },
          {
            name: "平",
            itemStyle: {
              barBorderRadius: 13
            },
            type: "bar",
            data: p
          },
          {
            name: "胜",
            type: "line",
            yAxisIndex: 1,
            data: s
          },
          {
            name: "负",
            type: "line",
            yAxisIndex: 1,
            data: f
          },
          {
            name: "平",
            type: "line",
            yAxisIndex: 1,
            data: p
          }
        ]
      };
    }
  }
};
</script>
<style lang="less">
.brokenLine {
  width: 100%;
  height: 400px;
  float: left;
  border-radius: 6px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  padding: 10px;
  padding-top: 40px;
  overflow: hidden;
}
</style>
